:root {
  --color-text-0: #fafafc;
  --color-text-1: #8c8c8e;
  --color-text-2: #646466;
  --color-text-3: #20202a;
  --color-text-4: #1e1e28;
  --color-text-5: #121217;

  --color-background-0: #191923;
  --color-background-1: #1e1e28e0;

  --color-box-shadow-0: #0f0f1433;
  --color-text-shadow-0: #fafafc66;

  --color-gradient-1-1: #1e1e28ed;
  --color-gradient-1-2: #1e1e28f5;
  --color-gradient-1-3: #1e1e28fc;
  --color-gradient-1-4: #1e1e28;

  --color-gradient-2-1: #252532fa;
  --color-gradient-2-2: #23232dfa;
}

/* ------------------------------ common ------------------------------ */
/* ----- fonts ----- */
/* ----- text defaults ----- */
body {
  color: #8c8c8e;
  color: var(--color-text-1);
  font-size: 12px;
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  letter-spacing: 0.5px;
  font-smooth: subpixel-antialiased;
}

/* ----- title defaults ----- */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  color: #fafafc;
  color: var(--color-text-0);
  margin-bottom: 0;
  font-family: 'Poppins', sans-serif;
}

h1 {
  font-size: 52px;
  font-weight: 800;
}

h4 {
  font-size: 17px;
  font-weight: 600;
}

h5 {
  font-size: 14px;
  font-weight: 600;
}

h6 {
  font-size: 12px;
  font-weight: 400;
}

/* ----- text config ----- */
.art-sm-text {
  font-size: 11px;
}

.art-lg-text {
  font-size: 16px;
}

.art-white {
  color: #fafafc;
  color: var(--color-text-0);
}

/* ----- container ----- */
.container-fluid {
  overflow: hidden;
  padding: 0 30px;
  position: relative;
}

/* ----- buttons frame ----- */
.art-buttons-frame a {
  margin-right: 15px;
}

/* ----- suptitle ----- */
.art-el-suptitle {
  margin-top: 5px;
  color: #646466;
  color: var(--color-text-2);
  font-style: italic;
  font-size: 11px;
}

/* ----- divider ----- */
.art-ls-divider {
  background: #646466;
  background: var(--color-text-2);
  width: 100%;
  height: 1px;
  opacity: 0.3;
}

/* ----- image defaults ----- */
.art-img-default {
  width: 100%;
  box-shadow: 0 3px 8px 0 #0f0f1433;
  box-shadow: 0 3px 8px 0 var(--color-box-shadow-0);
}

/* ----- link defaults ----- */
a {
  text-decoration: none;
  color: #8c8c8e;
  color: var(--color-text-1);
}
a:hover {
  text-decoration: none;
  color: #fafafc;
  color: var(--color-text-0);
}
a:focus {
  outline: inherit;
}

/* ----- table ----- */
.art-table {
  color: #8c8c8e;
  color: var(--color-text-1);
}
.art-table ul {
  padding: 0;
  margin: 0;
}
.art-table ul li {
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}
.art-table ul li:last-child {
  margin-bottom: 0;
}

/* ----- quote ----- */
blockquote {
  margin-top: 30px;
  margin-bottom: 30px;
  background: var(--color-primary-0);
  color: #191923;
  color: var(--color-background-0);
  padding: 30px;
  padding-left: 35px;
  font-weight: 400;
  font-style: italic;
  font-size: 14px;
  box-shadow: 0 1px 4px 0 #0f0f1433;
  box-shadow: 0 1px 4px 0 var(--color-box-shadow-0);
  position: relative;
}
blockquote:before {
  position: absolute;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  content: '\f10d';
  font-size: 18px;
  opacity: 0.5;
  top: 5px;
  left: 9px;
  color: #fafafc;
  color: var(--color-text-0);
}
blockquote:after {
  position: absolute;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  content: '\f10d';
  font-size: 18px;
  opacity: 0.5;
  bottom: 5px;
  right: 9px;
  color: #fafafc;
  color: var(--color-text-0);
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

/* ----- list ----- */
.art-custom-list {
  padding-left: 25px;
}
.art-custom-list li {
  position: relative;
  list-style-type: none;
  color: #fafafc;
  color: var(--color-text-0);
  margin-bottom: 5px;
}
.art-custom-list li:before {
  content: '';
  position: absolute;
  top: 4px;
  left: -25px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: solid 2px var(--color-primary-0);
  background: transparent;
  box-shadow: 0 3px 8px 0 #0f0f1433;
  box-shadow: 0 3px 8px 0 var(--color-box-shadow-0);
}

/* ------------------------------ link ------------------------------ */
.art-link {
  position: relative;
  text-transform: uppercase;
  font-size: 10px;
  display: inline-block;
  letter-spacing: 1.5px;
  font-weight: 600;
  margin-right: 15px;
  margin-bottom: 10px;
  color: #8c8c8e;
  color: var(--color-text-1);
  transition: 0.4s ease-in-out;
  cursor: pointer;
}
.art-link:hover {
  color: #fafafc;
  color: var(--color-text-0);
  text-shadow: 0 0 3px #fafafc66;
  text-shadow: 0 0 3px var(--color-text-shadow-0);
}
.art-link.art-white-link {
  color: #fafafc !important;
  color: var(--color-text-0) !important;
}
.art-link.art-white-link:hover {
  color: #fafafc !important;
  color: var(--color-text-0) !important;
  text-shadow: 0 0 3px #fafafc66;
  text-shadow: 0 0 3px var(--color-text-shadow-0);
  -webkit-transform: translateX(5px);
          transform: translateX(5px);
}
.art-link.art-color-link {
  color: var(--color-primary-0) !important;
}
.art-link.art-color-link:hover {
  color: var(--color-primary-1) !important;
