  .art-counter-frame {
    padding-left: 15%;
  }
}

/* ------------------------------ content (after 500px) ------------------------------ */
@media (max-width: 500px) {
  .art-section-title {
    flex-direction: column;
  }
  .art-section-title .art-right-frame {
    margin-left: auto;
    margin-right: auto;
  }
  .art-grid.art-grid-2-col .art-grid-item {
    width: 100%;
  }
  .art-grid.art-grid-3-col .art-grid-item {
    width: 100%;
  }
  .art-counter-frame {
    padding-left: 10%;
  }
  .art-counter-frame .art-counter-box {
    min-width: 30px;
  }
}

/* ------------------------------ burger button ------------------------------ */
.art-menu-bar-btn {
  margin-top: -10px;
  display: inline-block;
  width: 15px;
  height: 12px;
  padding: 30px;
}
.art-menu-bar-btn span,
.art-menu-bar-btn span:after,
.art-menu-bar-btn span:before {
  content: '';
  display: block;
  width: 15px;
  height: 3px;
  border-radius: 1px;
  background: #8c8c8e;
  background: var(--color-text-1);
  transition: 0.4s ease-in-out;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.art-menu-bar-btn span {
  position: relative;
  margin: 5px 0 0;
}
.art-menu-bar-btn span:after,
.art-menu-bar-btn span:before {
  position: absolute;
}
.art-menu-bar-btn span:before {
  top: -5px;
}
.art-menu-bar-btn span:after {
  top: 5px;
}
.art-menu-bar-btn.art-active span {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.art-menu-bar-btn.art-active span:before {
  -webkit-transform: translate(0px, 5px) rotate(-90deg);
          transform: translate(0px, 5px) rotate(-90deg);
}
.art-menu-bar-btn.art-active span:after {
  -webkit-transform: translate(0px, -5px) rotate(-90deg);
          transform: translate(0px, -5px) rotate(-90deg);
}
.art-menu-bar-btn:hover span,
.art-menu-bar-btn:hover span:after,
.art-menu-bar-btn:hover span:before {
  background: #fafafc;
  background: var(--color-text-0);
}

/* ------------------------------ transitions ------------------------------ */
.art-a {
  transition: 0.55s ease-in-out;
}

/* ----- in animation ----- */
.transition-fade {
  /* transition: 0.55s ease-in-out; */
  opacity: 1;
}
.transition-fade .art-a {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}
.transition-fade .art-price.art-popular-price {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

/* ----- out animation ----- */
html.is-animating .transition-fade {
  opacity: 0;
}
html.is-animating .transition-fade .art-a {
  -webkit-transform: scale(0.93);
          transform: scale(0.93);
  opacity: 0;
}
html.is-animating .art-a,
html.is-animating .art-current-page {
  opacity: 0;
}

/* ----- in animation ----- */
.fade-right {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
}

/* ----- out animation ----- */
html.is-animating .fade-right {
  -webkit-transform: translateX(-60px);
          transform: translateX(-60px);
  opacity: 0;
}

/* ----- in animation ----- */
.fade-left {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
}

/* ----- out animation ----- */
html.is-animating .fade-left {
  -webkit-transform: translateX(60px);
          transform: translateX(60px);
  opacity: 0;
}
@media (max-width: 920px) {
  .transition-fade .art-price.art-popular-price {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

